<template>
   <div class='root'>
      <action-bar title='商品库搜索'></action-bar>
      <div class="search">
         <div class="search-content">
            <van-icon name="search" color="#b4b4b4" class="search-icon" size="16px" />
            <input v-model="searchStr" type="text" maxlength="26" class="search-input" placeholder="搜索你想要的商品"
               bindconfirm="handleSearch" />
         </div>
         <van-button class="search-btn" @click="handleSearch" color="#42D18A">搜索</van-button>
      </div>
      <div class='container'>
         <scroll-view loadmore ref="scroll" class='content' :noMoreData="noMoreData" @scrolltolower="onLoadMore" :data="goodsList">
           <div>
            <goods-item v-for="(item, index) in goodsList" :key="item.skuCategoryEncode">
               <van-image radius=".25rem" slot="imgSlot" :src="baseUrl + item.imageUrl" class="goods-img" />
               <div slot="nameSlot">{{ item.spuName }}</div>
               <div slot="attributeSlot">
                  {{ item.categoryName }}
               </div>
               <div slot="menuSlot">
                  <div v-for="(childItem, childIndex) in item.items" :key="childItem.skuId">
                     <div v-if="childItem.libraryState == '1'" class="goods-released">
                        {{ childItem.skuName }}
                     </div>
                     <div v-else-if="childItem.libraryState == '0'" class="goods-not-check"
                        @click="bindClickGoods(index, childIndex)">
                        {{ childItem.skuName }}
                     </div>
                     <div v-else-if="childItem.libraryState == '2'" class="goods-checked"
                        @click="bindClickGoods(index, childIndex)">
                        {{ childItem.skuName }}
                     </div>
                  </div>
               </div>
            </goods-item>
           </div>
         </scroll-view>
      </div>
   </div>
</template>
<script>
import { Icon, Button } from 'vant'
import GoodsItem from '@/components/util/GoodsItem.vue'
import { libraryEdit, librarySearch } from '@/http/api'

export default {
  name: 'searchGoods',
  components: {
    GoodsItem,
    [Icon.name]: Icon,
    [Button.name]: Button
  },
  computed: {
    baseUrl () {
      return this.$store.state.baseUrl
    }
  },
  data () {
    return {
      searchStr: '',
      goodsList: [],
      noMoreData: false, // 没有更多数据了
      start: 0,
      limit: 10
    }
  },
  methods: {
    // 上拉加载更多
    onLoadMore () {
      if (this.noMoreData === true) {
        return
      }
      this.start = this.start + this.limit
      this.requestData()
    },
    requestData () {
      librarySearch({
        name: this.searchStr,
        start: this.start,
        limit: this.limit
      }).then(res => {
        this.$refs.scroll.loaded()
        let nmd = false
        if (res.data.length < 10) {
          nmd = true
        }
        const goodsList = this.goodsList
        goodsList.push(...res.data)
        this.noMoreData = nmd
        if (this.goodsList.length === 0) {
          this.$toast('未搜索到商品')
        }
      }).catch(rej => {
        this.$refs.scroll.loaded()
        this.$toast(rej.msg)
      })
    },
    // 搜索
    handleSearch () {
      if (this.searchStr.trim() === '') {
        this.$toast('请输入搜索内容')
        return
      }
      this.start = 0
      this.goodsList = []
      this.requestData()
    },
    bindClickGoods (pindex, cindex) {
      const goodsList = this.goodsList
      const item = goodsList[pindex].items[cindex]
      libraryEdit({
        skuId: item.skuId
      }).then(res => {
        if (item.libraryState) {
          item.libraryState = 0
        } else {
          item.libraryState = 2
        }
      }).catch(res => {
        this.$toast(res.msg)
      })
    }
  },
  mounted () {
  }
}
</script>
<style lang="scss" scoped>
.root {
   width: 100vw;
   height: 100vh;
   display: flex;
   flex-direction: column;
}

.container {
   flex-grow: 1;
   position: relative;
}

.content {
   position: absolute;
   left: 0;
   right: 0;
   top: 0;
   bottom: 0;
}

.search {
   height:3rem;
   box-sizing: border-box;
   padding: .375rem .875rem;
   display: flex;
   align-items: center;
}

.search-content {
   display: flex;
   flex-direction: row;
   background-color: #F2F2F2;
   border-radius: .1875rem;
   width: 82%;
   height: 100%;
   align-items: center;
}

.search-icon {
   margin-left: .375rem;
}

.search-input {
   width: 90%;
   max-lines: 1;
   color: #333333;
   height: 3rem;
   border: none;
   padding-left: .25rem;
   background: transparent;
}

.search-input::placeholder {
   color: #b4b4b4;
   font-size: .7813rem;
}

.search-btn {
   width: 20%;
   height: 100%;
   margin-left: .5rem;
}

.goods-img {
   width: 20vw;
   height: 20vw;
   border-radius: 0.25rem;
   margin: 0 0.25rem;
   background-color: #f2f2f2;
}

.goods-released {
   background-color: #f5f7f9;
   color: #999;
   padding: .375rem;
   border-radius: .375rem;
   margin: .375rem;
   display: inline-block;
   font-size: .875rem;
}

.goods-not-check {
   background-color: white;
   color: #333;
   padding: .3125rem .375rem;
   border-radius: .375rem;
   margin: .375rem;
   border: .0625rem solid #333;
   display: inline-block;
   font-size: .875rem;

}

.goods-checked {
   background-color: #42D18A;
   color: white;
   padding: .375rem;
   border-radius: .375rem;
   margin: .375rem;
   display: inline-block;
   font-size: .875rem;
}
</style>
